@-webkit-keyframes cardAnim {
  0% {
    border: 0.5vh solid rgb(30, 30, 30);
  }
  50% {
    border: 0.5vh solid rgb(40, 40, 40);
    border-radius: 1.5vh;
  }
  100% {
    border: 0.5vh solid rgb(30, 30, 30);
  }
}
@keyframes cardAnim {
  0% {
    border: 0.5vh solid rgb(30, 30, 30);
  }
  50% {
    border: 0.5vh solid rgb(40, 40, 40);
    border-radius: 1.5vh;
  }
  100% {
    border: 0.5vh solid rgb(30, 30, 30);
  }
}
.Card {
  color: gray;
  background-color: transparent;
  border: 0.5vh solid rgb(20, 20, 20);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 32vw;
  padding: 3vh 2vw;
  border-radius: 2vh;
  font-family: "Poppins", sans-serif;
  margin-left: 5vh;
  -webkit-animation-name: cardAnim;
          animation-name: cardAnim;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
@-webkit-keyframes cardTitleAnim {
